import React from 'react'
import ContactForm from '../../components/ContactForm'
import CalEmbed from '../../components/CalEmbed'

export const metadata = { title: 'Contact — Resonaré Chamber Choir', description: 'Contact our Artistic Team — schedule auditions or send questions.' }

export default async function Contact({ searchParams }: { searchParams?: Record<string, string | string[]> | Promise<Record<string, string | string[]>> }) {
  const resolved = await searchParams
  const raw = resolved?.summary
  const summary = Array.isArray(raw) ? raw[0] : (raw || '')

  return (
    <div>
      <section className="py-8">
        <h1 className="text-3xl font-semibold">Contact our Artistic Team</h1>
        <p className="text-neutral-400 mt-2">We’re happy to hear from you for questions about auditions, repertoire, or performances.</p>
      </section>

      <section className="py-8">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          <div>
            <h2 className="text-xl font-semibold">Send Us a Message</h2>
            <div className="mt-4">
              <ContactForm defaultSummary={summary || ''} />
            </div>
          </div>
          <div>
            <h2 className="text-xl font-semibold">Schedule an Audition with Us</h2>
            <p className="mt-2 text-neutral-400">Use the calendar below to pick a date for an audition. (Calendly placeholder)</p>
            <div id="schedule" className="mt-4">
              <CalEmbed />
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}
